@import '../../_variables.sass';

table {
  padding-top: 30px;
  border-spacing: 0px; }

th {
  border-bottom: 2px solid $smokeBlack;
  padding: 10px;
  text-align: center;

  &:hover,
  &:active,
  &:focus {
    cursor: pointer;
    color: $red; }

  & p::after {
    content: '\f0d7';
    font-family: 'Font Awesome 5';
    font-weight: 900;
    padding-left: 5px; } }

td {
  border-bottom: 2px solid $smokeBlack;
  padding: 10px;
  text-align: center; }

.stat-active {
  color: $red !important;

  &.descending {
    & p::after {
      content: '\f0d7';
      font-family: 'Font Awesome 5';
      font-weight: 900;
      padding-left: 5px; } }

  &.ascending {
    & p::after {
      content: '\f0d8';
      font-family: 'Font Awesome 5';
      font-weight: 900;
      padding-left: 5px; } } }

.table-image-header {
  &::after {
    content: none !important; } }

// for touch devices
@media (hover: none) {
  .container {
    overflow-x: scroll; }

  th {
    &:hover,
    &:active,
    &:focus {
      color: white; } }

  -webkit-overflow-scrolling: auto;

  ::-webkit-scrollbar {
    width: 12px !important; } }
