@import '../../_variables.sass';

.compare-button-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "RDRLino";
  background-color: $black;
  border: 0;
  font-size: 20px;
  position: fixed;
  left: 0px;
  bottom: 0px;
  padding: 0;
  top: 90%;
  width: 100%;

  a {
    color: $red;
    text-decoration: none;

    &:active,
    &:hover {
      color: white; } }


  &:focus {
    outline: none; } }
